html {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.human {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 40px 30px;
  padding: 40px 35px;
  display: grid;
}

@media (width >= 960px) {
  .human {
    grid-template-columns: repeat(4, 200px);
  }
}

.wrapper {
  position: relative;
}

.person {
  transform-origin: 50% 115px;
  border-radius: 10px;
  margin: -10px -10px -16px;
  padding: 10px 10px 16px;
  transition: transform 1s;
}

.person:hover {
  z-index: 1;
  background: #eee;
  position: absolute;
  transform: scale(1.2);
}

.person__extra {
  margin-top: 9px;
  font-size: 18px;
  line-height: 21px;
  display: none;
}

.person:hover .person__extra {
  display: block;
}

.person__image {
  border-radius: 50%;
  width: 100%;
  transition: border-radius 1s;
  display: block;
}

.person:hover .person__image {
  border-radius: 25%;
}

.person__photo {
  position: relative;
}

.person__rank {
  --rank-size: 32px;
  right: 10px;
  top: calc(50% - var(--rank-size) / 2);
  width: var(--rank-size);
  height: var(--rank-size);
  font-size: 16px;
  font-weight: bold;
  line-height: var(--rank-size);
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
}

.person__details {
  text-align: center;
  margin-top: 8px;
}

.person__name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
}

.person__sport:after {
  content: ",";
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "M";
}
